.nav {
  &-top {
    grid-column: 7 span;
    justify-self: right;
    align-self: center;

    &__list {
      display: flex;
      column-gap: 64px;
    }

    &__link {
      font-size: 14px;
      font-weight: 400;
      line-height: 100%;
      color: var(--grey-color);
    }
  }

  &-main {
    grid-column: 9 span;
    grid-row: 2;
  }
}
