.header {
  background: linear-gradient(var(--grey_light-color) 50px, var(--white-color) 50px 100%);

  &__container {
    grid-template-rows: 50px 91px 60px;
  }

  &__phone {
    @include flex-v-center;

    grid-column: 5 span;
    align-self: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: var(--secondary-color);
  }

  &__wrap {
    grid-column: 2 span;
    grid-row: 3;
  }
}

.logo {
  grid-column: 3 span;
  grid-row: 2;
}
